import { useEffect, useState } from "react";
import { useHistory, useLocation } from "react-router-dom";
import "./App.css";
import Timer from "./components/Timer";
import Yearbook from "./components/Yearbook";
import Form from "./components/Form";
import Closed from "./components/Closed";

import User from "./components/User";
import { startdate, enddate } from "./config";
import yearbook from "./yearbook.json";

function App() {
	const [time, setTime] = useState(Date.now());
	useEffect(() => {
		const interval = setInterval(() => {
			setTime(Date.now());
		}, 1000);
		return () => clearInterval(interval);
	}, []);

	const [component, setComponent] = useState(null);
	const location = useLocation();
	useEffect(() => {
		const component = new URLSearchParams(location.search.substr(1)).get(
			"component"
		);
		setComponent(component);
	}, [location]);
	const [page, setPage] = useState(0);

	const history = useHistory();
	useEffect(() => {
		window.scrollTo(0, 0);
	}, [location, page]);

	return (
		<div className="App">
			{component === "wall" ? (
				<User />
			) : component === "scribble" ? (
				<>
					<h1 style={{ color: "#00000095" }}>Scribble!</h1>
					{time < Date.parse(startdate) ? (
						<Timer />
					) : time > Date.parse(enddate) ? (
						<Closed />
					) : (
						<Form yearbook={yearbook} history={history} />
					)}
				</>
			) : (
				<Yearbook page={page} setPage={setPage} />
			)}
		</div>
	);
}

export default App;
