import React, { useEffect, useState } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import yearbook from "../yearbook.json";
import axios from "../api";
import "./Yearbook.css";
import Loader from "./Loader";

function shortenQuote(quote) {
	const limit = 200;
	if (quote.length > limit) return quote.substr(0, limit - 3) + "...";
	return quote;
}

export default function User({ page, setPage }) {
	const [loading, setLoading] = useState(false);
	const [freq, setFreq] = useState([]);

	const location = useLocation();

	useEffect(() => {
		setLoading(true);
		axios
			.get(`/posts`)
			.then(({ data }) => {
				if (data?.length) {
					const freq = {};
					data.forEach(({ roll }) => {
						if (roll in freq) freq[roll] += 1;
						else freq[roll] = 1;
					});
					setFreq(freq);
				}
			})
			.catch(()=>{})
			.finally(() => setLoading(false));
	}, [location]);
	const history = useHistory();

	const pageSize = 8;
	const totalPages = Math.ceil(yearbook.length / pageSize);

	if (loading)
		return (
			<div id="yearbook">
				<h1 style={{ marginBottom: "3rem" }}>Yearbook 2021 📖</h1>
				<Loader />
			</div>
		);
	return (
		<div id="yearbook">
			<h1 style={{ marginBottom: "3rem" }}>Yearbook 2021 📖</h1>
			<div className="holder">
				{yearbook.map(({ roll, name, nickname, line }, index) => {
					const lowerIndex = pageSize * page;
					const upperIndex = lowerIndex + pageSize;
					if (lowerIndex <= index && index < upperIndex)
						return (
							<div
								className="yb-card"
								onClick={() => {
									history.push(`?component=wall&roll=${roll}`);
								}}
							>
								{/* <a className="yb-card" href={`?component=wall&roll=${roll}`}> */}
								<img
									src={`yb_photos_400/${roll}.jpg`}
									alt="profile"
									className="profile-picture"
								/>
								<p className="name">{name}</p>
								<p className="nickname">{nickname}.</p>
								<p className="quote">{shortenQuote(line)}</p>
								{freq[roll] && <p>{freq[roll] ?? 0} 📝</p>}
								<span className="tooltiptext">Go to {roll}'s wall</span>
								{/* </a> */}
							</div>
						);
					return null;
				})}
			</div>
			<p className="preview-text">
				Write a scribble on their walls{" "}
				<Link  to={`?component=scribble`}> here!</Link>
			</p>
			<div className="pagination">
				<button onClick={() => setPage(page - 1)} disabled={page === 0}>
					Prev
				</button>
				<p>{page + 1}</p>
				<button
					onClick={() => setPage(page + 1)}
					disabled={page === totalPages - 1}
				>
					Next
				</button>
			</div>
		</div>
	);
}
