import React, { Component } from "react";
import ReCAPTCHA from "react-google-recaptcha";

import Preview from "./Preview";
import axios from "../api";

class Form extends Component {
	constructor() {
		super();
		this.state = {
			roll:
				new URLSearchParams(window.location.search.substr(1)).get(
					"init_roll"
				) ?? "",
			message: "",
			error: "",
			loading: false,
		};
		this.hiddenFileInput = React.createRef(null);
		this.recaptchaRef = React.createRef(null);
	}

	handleChange = (event) => {
		this.setState({ [event.target.id]: event.target.value });
	};

	submitPostData = async () => {
		const { message, roll } = this.state;
		const data = { message, roll };
		const recaptchaValue = this.recaptchaRef.current.getValue();

		this.setState(
			{
				error: !roll
					? "Receiver's name is required."
					: !message.length
					? "Message field is required"
					: !recaptchaValue
					? "Sorry, we are not taking response from robots yet!"
					: "",
			},
			() => {
				const { error } = this.state;
				if (!error) {
					this.setState({ loading: true }, () => {
						axios
							.post(`/add_post/`, data)
							.then(() => {
								this.clearForm();
								this.setState({ completed: true });
								this.props.history.push("?component=wall&roll=" + roll);
							})
							.catch((error) => {
								this.setState({ error: error?.message ?? error });
							})
							.finally(() => this.setState({ loading: false }));
					});
				}
			}
		);
	};

	clearForm = () => {
		this.setState({
			roll: "",
			message: "",
			error: "",
			loading: false,
		});
	};

	render() {
		const { roll, error, message, loading, completed } = this.state;
		const { yearbook } = this.props;

		return (
			<>
				{completed ? (
					<p className="preview-text">
						You have scribbled your farewell, leave the rest to us... 🥳
					</p>
				) : (
					<div className="form-page">
						<div id="form">
							<div className="card">
								<select id="roll" value={roll} onChange={this.handleChange}>
									<option value="">Choose the Wall</option>
									{yearbook.map(({ roll, name, branch }) => {
										return (
											<option value={roll}>
												{name} {branch}
											</option>
										);
									})}
								</select>
								<div className="divider" />
								<textarea
									type="text"
									id="message"
									autoComplete="off"
									value={message}
									onChange={this.handleChange}
									placeholder="Pour your heart out!"
								/>
							</div>
							<ReCAPTCHA
								sitekey="6LfJ0WMaAAAAAO9K5Lj8DrYFLfR2bAwav1-w_XQs"
								onChange={this.recaptcha}
								ref={this.recaptchaRef}
							/>
							{error && <p id="error-para">{error}</p>}
							<div>
								<button
									id="submit"
									disabled={loading}
									onClick={this.submitPostData}
								>
									Post
								</button>
								<button id="clear" onClick={this.clearForm}>
									Clear
								</button>
							</div>
						</div>
						<Preview {...this.state} />
					</div>
				)}
				<p className="preview-text">
					For assistance/queries reachout to{" "}
					<a
						href="mailto:wellbeing@iitj.ac.in"
						target="_blank"
						rel="noreferrer"
					>
						us
					</a>{" "}
					or mail{" "}
					<a
						href="mailto:tawatia.1@iitj.ac.in"
						target="_blank"
						rel="noreferrer"
					>
						dev
					</a>{" "}
					to report.
				</p>
			</>
		);
	}
}

export default Form;
