import React, { useEffect, useState } from "react";
import { useHistory, useLocation } from "react-router";
import { ArrowLeft } from "react-feather";
import Scribble from "./ScribbleNote";
import yearbook from "../yearbook.json";
import axios from "../api";
import "./User.css";
import Loader from "./Loader";
import { Link } from "react-router-dom";

export default function User() {
	const [loading, setLoading] = useState(true);
	const [user, setUser] = useState({});
	const [data, setData] = useState([]);

	const location = useLocation();
	useEffect(() => {
		setLoading(true);
		const roll = new URLSearchParams(location.search.substr(1)).get("roll");
		yearbook.forEach((entry) => {
			if (entry.roll === roll) setUser(entry);
		});
		axios
			.get(`/posts`)
			.then(({ data }) => {
				if (data?.length) {
					setData(data.filter((entry) => entry.roll === roll));
				}
			})
			.catch(()=>{})
			.finally(() => setLoading(false));
	}, [location]);
	const history = useHistory();

	if (loading) return <Loader />;
	const { name, nickname, line, about, roll } = user;
	return (
		<div className="wall">
			<button
				onClick={() => {
					history.push("?component=yearbook");
				}}
			>
				<ArrowLeft size={24} />
				Yearbook
			</button>
			<h1 style={{marginTop: '2rem'}}>{name}</h1>

			<div className="yb-card">
				<img
					src={`yb_photos/${roll}.jpg`}
					alt="profile"
					className="profile-picture"
				/>
				<div className="description">
					<p className="nickname">{nickname}.</p>
					<p className="quote">{line}</p>
					<p className="about">{about}</p>
				</div>
			</div>
			<div className="divider" />
			<h1>Wall 🧱</h1>
			<div className="holder">
				{data.map((entry) => {
					return <Scribble {...entry} />;
				})}
			</div>
			<p className="preview-text">
				Write a scribble on this wall
				<Link to={`?component=scribble&init_roll=${roll}`}> here!</Link>
			</p>
			<p className="preview-text">
				For assistance/queries reachout to{" "}
				<a href="mailto:wellbeing@iitj.ac.in" target="_blank" rel="noreferrer">
					us
				</a>{" "}
				or mail{" "}
				<a href="mailto:tawatia.1@iitj.ac.in" target="_blank" rel="noreferrer">
					dev
				</a>{" "}
				to report.
			</p>
		</div>
	);
}
