import React, { useState } from "react";
import "./ScribbleNote.css";

export default function Scribble({ message }) {
 const [rotation] = useState(Math.ceil(Math.random() * 12) - 6);
	return (
		<div className="note-container">
			<div className={`note note-rotation-${rotation}`}>
				<div className="note-header" />
				<p className="text">{message}</p>
			</div>
		</div>
	);
}
